.cardsDonation {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 10px;
}

.cardD {
    height: 40vh;
    background-color: #191c29;
    width: 100%;
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: medium;
}
.parentCard{
   font-size: small;  
   color: rgb(88 199 250 / 100%);

}
.card__image {
    height: 160px;
    width: 160px;
    border-radius: 50%;
    border: 5px solid #272133;
    margin-top: 20px;
    box-shadow: 0 10px 50px rgba(235, 25, 110, 1);
    margin-bottom: 2rem;
}