
:root {
    --card-height: 50vh;
    --card-width: calc(var(--card-height) / 1.5);
} 


.card {
    background: #212534;
    display: flex;
    align-items: center;
    flex-direction: column;
    padding-top: 2rem;
    padding-bottom: 2rem;
    box-sizing: border-box;
}
body{

        background: #212534;
        display: flex;
        align-items: center;
        flex-direction: column;
        padding-top: 2rem;
        padding-bottom: 2rem;
        box-sizing: border-box;

}

.card {
    display: flex;
    background: #191c29;
    width: 100%;
    height: var(--card-height);
    padding: 10px;
    position: relative;
    border-radius: 6px;
    justify-content: center;
    align-items: center;
    text-align: center;
    display: flex;
    font-size: 1.5em;
    color:rgb(88 199 250 / 100%);
    cursor: pointer;
    font-family: cursive;
}

h2 ,li,ul,h3{
    background-color:#191c29;
    font-size: large;
}
li{
    list-style: none;
}
